
<template>
  <nav>
    <v-form ref="form" v-model="valid" lazy-validation>
      <v-text-field v-model="name" :counter="10" :rules="nameRules" label=" Name" required></v-text-field>

      <v-text-field
       v-model="email"
        :rules="emailRules"
         label=" E-mail" 
         required
         ></v-text-field>

      <v-text-field
        v-model="name"
        :counter="10"
        :rules="companyRules"
        label="Company Name"
        required
      ></v-text-field>

      <v-select
        v-model="select"
        :items="devices"
        :rules="[v => !!v || 'Item is required']"
        label=" Item"
        required
      ></v-select>

      <v-checkbox
        v-model="checkbox"
        :rules="[v => !!v || 'You must agree to continue!']"
        label="Do you agree?"
        required
      ></v-checkbox>

      <v-btn :disabled="!valid" color="info" class="mr-4" @click="validate">Validate</v-btn>

      <v-btn color="error" class="mr-4" @click="reset">Reset Form</v-btn>

      <v-btn color="warning" @click="resetValidation">Reset Validation</v-btn>
    </v-form>
  </nav>
</template>

<script>
export default {
  data: () => ({
    valid: true,
    name: "",
    nameRules: [
      v => !!v || "Name is required",
      v => (v && v.length <= 20) || "Name must be less than 10 characters"
    ],
    email: "",
    emailRules: [
      v => !!v || "E-mail is required",
      v => /.+@.+\..+/.test(v) || "E-mail must be valid"
    ],
    select: null,
    devices: ["Device 1", "Device 2", "Device 3", "Device 4"],
    companyName: "",
    companyRules: [
      v => !!v || "Name is required",
      v => (v && v.length <= 25) || "Name must be less than 10 characters"
    ],
    checkbox: false
  }),

  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        this.snackbar = true;
      }
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    }
  }
};
</script>