<template>
  <v-row justify="center">
    <v-btn
    class="ma-4"
      fab
      color="#fff"
      light
      @click.stop="dialog = true"
    >
    <v-icon>add</v-icon>
    </v-btn>

    <v-dialog
      v-model="dialog"
      max-width="600px"
    >
      <v-card>
        <v-card-title justify="center" light class="headline">Add new data entry?</v-card-title>

        <v-card-text>
                <v-form class="px-3">
                 <v-text-field autocomplete="off" prepend-icon="" label="Name" v-model="name" ></v-text-field>
                 <v-text-field autocomplete="off" prepend-icon="" label="EmployeeId" v-model="employee_id" ></v-text-field>
                 <v-text-field autocomplete="off" prepend-icon="" label="Company" v-model="company" ></v-text-field>
                 <v-text-field autocomplete="off" prepend-icon="" label="State" v-model="state" ></v-text-field>
                 <v-text-field autocomplete="off" prepend-icon="" label="Status" v-model="status" ></v-text-field>
                 
                </v-form>
            </v-card-text>

        <v-card-actions>
          <div class="flex-grow-1"></div>
                    
            <v-btn
              @click="submit"
              class="success mx-0 mt-3"
              >Add Entry</v-btn>

        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import db from '@/components/fbInit'

  export default {
    data () {
      return {
        dialog: false,
            name: '',
            employee_id: '',
            company: '',
            state: '',
            status: '',

      }
    },
    methods: {
        submit(){
            const users = {
            name: this.name,
            employee_id: this.employee_id,
            company: this.company,
            state: this.state,
            status: this.status
        }
        db.collection('users').add(users).then(() => {
            alert('Succesfully added to database')
        } 

        
        )
    }
    }
  }
</script>
