<template>
  <div id="app">
    
<v-container  class="my-5">
<v-card raised align="center">
    <v-card-title flat class="pa-2" v-for="user in users" :key="user.id">
        <v-layout row wrap :class="`pa-3 user ${user.status}`">
          <v-flex xs12 md6>
            <div class="caption grey--text">Name</div>
            <div>{{user.name}}</div>
          </v-flex>
          <v-flex xs6 sm4 md2>
            <div class="caption grey--text">Company</div>
            <div>{{user.company}}</div>
          </v-flex>
          
          <v-flex xs6 sm4 md2>
            <div class="caption grey--text">User ID</div>
            <div>{{user.employeeId}}</div>
          </v-flex>
          <v-flex class="right" xs2 sm4 md2>
            <div class="right">
              <v-chip  small ripple  :class="`${user.status} caption my-3`">
                {{user.status}}
              </v-chip>
            
            </div>
            
          
          </v-flex>
        </v-layout>
     </v-card-title>
</v-card>
</v-container>

  <v-flex>
    <Popup />
  </v-flex>

  </div>
</template>


<script>
import Popup from '@/components/Popup'
import db from '@/components/fbInit'

export default {
  components: { Popup },
  data() {
    return {
    users: []
    }
  }, 
  created () {
db.collection('users').orderBy('status').get
().then(querySnapshot => {
  querySnapshot.forEach(doc => {
    const data = {
      'id': doc.id,
      "employeeId": doc.data().employee_id,
      'company': doc.data().company,
      'name': doc.data().name,
      'state':  doc.data().state,
      'status':  doc.data().status
    }
    this.users.push(data)
  });
})
 }
  };
</script>

<style scoped>
 
 .user.complete {
   border-left: 4px solid rgb(5, 221, 236);
 }
 .user.ongoing {
   border-left: 4px solid orange;
 }
 .user.overdue {
   border-left: 4px solid rgb(201, 5, 80);
 }

.v-chip.complete {
   border-left: 4px solid rgb(5, 221, 236);
 }
 .v-chip.ongoing {
   border-left: 4px solid orange;
 }
 .v-chip.overdue {
   border-left: 4px solid rgb(201, 5, 80);
 }


</style>