
<template>
  <v-app id="app">
    <v-content>
      <v-container>
        <v-row align="center" justify="center">
          <v-col cols="12" sm="10" md="8" xs="4">
            <v-card class="elevation-12">
              <v-toolbar dark color="info" flat>
                <v-toolbar-title>Sign up for Grace Cloud Watch</v-toolbar-title>
                <v-spacer></v-spacer>
                 </v-toolbar>
                  <v-card-text>
                   <v-form ref="form" v-model="validate" lazy-validation>
                  
                                <v-text-field
                                  full
                                  width
                                  color="info"
                                  aria-autocomplete="false"
                                  autocomplete="false"
                                  v-model="email"
                                  :rules="emailRules"
                                  label=" E-mail"
                                  prepend-icon="alternate_email"
                                  type="text"
                                  required
                                ></v-text-field>

                                <v-text-field
                                  full
                                  width
                                  color="info"
                                  v-model="password"
                                  :rules="passwordRules"
                                  label="Password"
                                  type="password"
                                  prepend-icon="lock"
                                  required
                                ></v-text-field>

                                <!-- <v-text-field
                                  color="info"
                                  v-model="company"
                                  :counter="10"
                                  :rules="companyRules"
                                  label="Company Name"
                                  prepend-icon="business"
                                  required
                                ></v-text-field>-->

                                <!-- <v-select 
                                  color="info"
                                  item-color="#fff"
                                  v-model="select"
                                  :items="status"
                                  :rules="[v => !!v || 'Item is required']"
                                  label=" Status"
                                  prepend-icon="group"
                                  required
                                ></v-select>-->

                                <v-checkbox
                                  color="info"
                                  v-model="checkbox"
                                  :rules="[v => !!v || 'You must agree to continue!']"
                                  label="Do you agree to ... ?"
                                  prepend-icon="flag"
                                  required
                                ></v-checkbox>
                              <v-spacer></v-spacer>

                  <v-layout align-end justify-end>
                    <v-card-text class="my-n4 mx-n2"><router-link class="" to="/login">Already have an account?</router-link></v-card-text>
                      <v-spacer></v-spacer>
                    <v-btn @click="register" class="ma-1 my-1" color="info" light>Register</v-btn>
                    <v-btn color="error" class="ma-1 my-1" @click="reset">Reset Form</v-btn>
                  </v-layout>
                </v-form>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</template>
                 

<script>
import firebase from "firebase";
export default {
  name: "register",
  props: {
    source: String
  },
  data: () => ({
    valid: true,
    name: "",
    nameRules: [
      v => !!v || "Name is required",
      v => (v && v.length <= 20) || "Name must be less than 10 characters"
    ],
    password: "",
    passwordRules: [
      v => !!v || "Password must be atleast 8 character in length",
      v => (v && v.length >= 8) || "atleast 8 characters"
    ],
    email: "",
    emailRules: [
      v => !!v || "E-mail is required",
      v => /.+@.+\..+/.test(v) || "E-mail must be valid"
    ],
    select: null,
    status: ["Owner", "Manager", "Employee", "End User"],
    companyName: "",
    companyRules: [
      v => !!v || "Name is required",
      v => (v && v.length <= 25) || "Name must be less than 10 characters"
    ],
    checkbox: false
  }),

  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        this.snackbar = true;
      }
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    },
    register: function(e) {
      // eslint-disable-next-line
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.email, this.password)
        .then(
          // eslint-disable-next-line
          user => {
            
            alert(`Hello ${firebase.auth().currentUser.email} , Your account has been created !`);
            this.$router.go({path: this.$router.path});
          },
          err => {
            alert("Oops. " + err.message);
          }
        );
        e.preventDefault();
        
    }
  }
};
</script>