
<template> 
<!-- eslint-disable vue/no-use-v-if-with-v-for,vue/no-confusing-v-for-v-if -->
  <nav>
    <v-app-bar app clipped-left hover>
      <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
      <v-toolbar-title>Grace Cloud Watch</v-toolbar-title>
      <v-spacer></v-spacer>



      <v-btn class="ma-2" dark disabled v-if ="isLoggedIn" >{{currentUser}} </v-btn>
<!-- eslint-disable-next-line -->
      <v-btn v-if ="!isLoggedIn" light class="ma-2" v-for="link in login" :key="link.text" :to="link.route">
        {{link.text}}
        <v-icon s left>fingerprint</v-icon>Login
      </v-btn>

      <v-btn v-if ="!isLoggedIn" light class="ma-2" v-for="link in register" :key="link.text" :to="link.route">
        {{link.text}}
        <v-icon left>person_add</v-icon>Register
      </v-btn>


      <v-btn v-if="isLoggedIn" @click="logout" light class="ma-2">
       Logout <v-icon right>keyboard_return</v-icon>
      </v-btn>


      <!-- <v-btn v-if="!isLoggedIn" light class="ma-2" v-for="link in login" :key="link.text" :to="link.route">
        {{link.text}}
        <v-icon s left>fingerprint</v-icon>Login
      </v-btn> -->
      <!-- <v-btn v-if="!isLoggedIn" light class="ma-2" v-for="link in register" :key="link.text" :to="link.route">
        {{link.text}}
        <v-icon left>person_add</v-icon>Register
      </v-btn>
      <v-btn v-if="isLoggedIn" @click="logout" light class="ma-2">
       Logout <v-icon right>keyboard_return</v-icon>
      </v-btn> -->



    </v-app-bar>

    <v-navigation-drawer v-if="isLoggedIn" v-model="drawer" app clipped absolute light>
      <v-list two-line dense>
        <v-list-tile v-for="link in links" :key="link.text">
          <v-list-item class="pt--1" color="grey darken-4" v-on: click :to="link.route">
            <v-list-item-action>
              <v-icon>{{link.icon}}</v-icon>
            </v-list-item-action>
            <v-list-item-content :to="link.route">
              <v-list-item-title>{{link.text}}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-divider></v-divider>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
  </nav>
</template>

<script>
import firebase from 'firebase'

export default {
name: 'navbar',
  data() {
    return {
      isLoggedIn: false,
      currentUser: false,
      drawer: true,
      links: [
        { icon: "dashboard", text: "Dashboard", route: "/" },
        { icon: "account_box", text: "Account", route: "/account" },
        { icon: "devices", text: "Database", route: "/devices" },
        { icon: "description", text: "Logs", route: "/logs" },
        { icon: "map", text: "Google Maps", route: "/gmap" },
             ],
      login: [
        {route: "/login"}
      ],
      register: [
        {route: "/register"}
      ],
    };
  },
  created() {
    if(firebase.auth().currentUser){
      this.isLoggedIn = true;
      this.currentUser = firebase.auth().currentUser.email;
    }
  },
  methods: {
    logout: function() {
      firebase.auth().signOut().then(() => 
      this.$router.go({path: this.$router.path})
      )
    }
}};
</script>