<template>
  <v-app id="app">
    <v-content>
      <v-container>
        <v-row align="center" justify="center">
          <v-col cols="12" sm="10" md="8" xs="4">
            <v-card class="elevation-12">
              <v-toolbar dark color="success" flat>
                <v-toolbar-title>Login to Grace Cloud Watch</v-toolbar-title>
                <v-spacer></v-spacer>
                 </v-toolbar>
                  <v-card-text>
                   <v-form ref="form" v-model="validate" lazy-validation>
                  <v-text-field
                    color="info"
                    label="E-mail"
                    v-model="email"
                    name="email"
                    :rules="emailRules"
                    prepend-icon="alternate_email"
                    type="text"
                  ></v-text-field>

                  <v-text-field
                    color="info"
                    id="password"
                    label="Password"
                    v-model="password"
                    :rules="passwordRules"
                    name="password"
                    prepend-icon="lock"
                    type="password"
                  ></v-text-field>

                  <v-spacer></v-spacer>

                  <v-layout align-end justify-end>
                    <v-card-text class="my-n4 mx-n2"><router-link class="" to="/reset">Forgot Password?</router-link></v-card-text>
                    
                <v-spacer></v-spacer>
                    <v-btn @click="login" class="ma-1 my-1" color="success" light>Login</v-btn>
                    <v-btn color="error" class="ma-1 my-1" @click="reset">Reset Form</v-btn>
                  </v-layout>
                </v-form>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import firebase from "firebase";

export default {
  name: "login",
  props: {
    source: String
  },
  data: function() {
    return {
      drawer: false,
      email: '',
      emailRules: [
        v => !!v || "E-mail is required",
        v => /.+@.+\..+/.test(v) || "E-mail must be valid"
      ],
      password: '',
      passwordRules: [
        v => !!v || "Name is required",
        v => (v && v.length >= 8) || "atleast 8 characters"
      ],
      register: [
        {
          route: "/register"
        }
      ]
    };
  },
  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        this.snackbar = true;
      }
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    },
    login: function(e) {
      // eslint-disable-next-line
      firebase
        .auth()
        .signInWithEmailAndPassword(this.email, this.password)
        .then(
          // eslint-disable-next-line
          user => {
            
            alert(`You are now logged in as ${firebase.auth().currentUser.email}`);
            this.$router.go({path: this.$router.path});
          },
          err => {
            alert("Oops. " + err.message);
          }
        );
        e.preventDefault();
        
    }
  }
};
</script>