<template>
  <v-app id="app">
    <v-content>
      <v-container>
        <v-row align="center" justify="center">
          <v-col cols="12" sm="10" md="8" xs="4">
            <v-card class="elevation-12">
              <v-toolbar light flat>
                <v-toolbar-title>Reset Password</v-toolbar-title>
                <v-spacer></v-spacer>
                 </v-toolbar>
                  <v-card-text>
                   <v-form ref="form" v-model="validate" lazy-validation>
                  <v-text-field
                    color="info"
                    label="E-mail"
                    v-model="email"
                    name="email"
                    :rules="emailRules"
                    prepend-icon="alternate_email"
                    type="text"
                  ></v-text-field>

                  <!-- <v-text-field
                    color="info"
                    id="password"
                    label="Password"
                    v-model="password"
                    :rules="passwordRules"
                    name="password"
                    prepend-icon="lock"
                    type="password"
                  ></v-text-field> -->

                  <v-spacer></v-spacer>

                  <v-layout align-end justify-end>
                    <v-card-text class="my-n4 mx-n2"><router-link class="" to="/register">Need an account?</router-link></v-card-text>
                <v-spacer></v-spacer>
                    <v-btn @click="resetPassword" class="ma-1 my-1" color="success" light>Send Reset Email</v-btn>
                    <!-- <v-btn color="error" class="ma-1 my-1" @click="resetPassword">Reset Form</v-btn> -->
                  </v-layout>
                </v-form>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import firebase from "firebase";


export default {
  name: "resetPassword",
  props: {
    source: String
  },
  data: function() {
    return {
      drawer: false,
      email: '',
      emailRules: [
        v => !!v || "E-mail is required",
        v => /.+@.+\..+/.test(v) || "E-mail must be valid"
      ],
            register: [
        {
          route: "/register"
        }
      ]
    };
  },
  methods: {
    
    resetPassword: function(){

var auth = firebase.auth();
// var emailAddress = "user@example.com";

auth.sendPasswordResetEmail(this.email).then(function() {
   alert("Please check your email for a password reset link ");// Email sent.
}).catch(function(error) {
  alert("Oops. " + err.message);// An error happened.
});

    },
        
    
  }
};
</script>